const apiUrl = 'https://utility.pinkytravels.com/api/UtilityAPI/';
const FetchBillPlan = 'FetchBillPlan';
const DisplayCouponCode = "DisplayCouponCode";
const ApplyCouponCode = "ApplyCouponCode";
const getPaymentMethod = "GetPaymentMethod";
const headers = { 'Content-Type': 'application/json' };
export const userService = {
    getCategoriesList: async (endPoint) => {
        try {
            const response = await fetch(apiUrl + endPoint, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
            });
            const json = await response.json();
            const categorylists = json.paymentCategory;
            const responseCategories = categorylists.map((category) => {
                category.slug = category.PayCategory.toLowerCase()
                    .replace(/[^\w ]+/g, '')
                    .replace(/ +/g, '-');
                return category;
            });

            return responseCategories;
        } catch (error) {
            console.error(error);
        }
    },
    getGetSubCategoryList: async (endPoint, categoryId) => {
        try {
            const response = await fetch(apiUrl + endPoint + '?CategoryID=' + categoryId, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
            });
            const json = await response.json();
            const subCategoryList = json.categorylists;
            return subCategoryList;
        } catch (error) {
            console.error(error);
        }
    },
    getGetOperatorDetailsList: async (endPoint, number) => {
        try {
            const response = await fetch(apiUrl + endPoint + '?Number=' + number, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
            });
            const json = await response.json();
            return json;
        } catch (error) {
            console.error(error);
        }
    },
    getGetCircleList: async (endPoint, billerid) => {
        try {
            const response = await fetch(apiUrl + endPoint + '?billerid=' + billerid, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
            });
            const json = await response.json();
            const circles = json.circles;
            return circles;
        } catch (error) {
            console.error(error);
        }
    },
    allFetchPrepaidPlan: async (endPoint, data) => {
        try {
            const response = await fetch(apiUrl + endPoint, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(data)
            });
            const json = await response.json();
            return json;
        } catch (error) {
            console.error(error);
        }
    },
    fetchBillPlanList: async (data) => {

        try {
            const response = await fetch(apiUrl + FetchBillPlan, {
                method: 'POST',
                headers:headers,
                body: JSON.stringify(data),
            });
            const json = await response.json();
            return json;
        } catch (error) {
            console.error(error);
        }
    },
    DisplayCouponCode: async (data) => {
        try {
            const response = await fetch(apiUrl + DisplayCouponCode, {
                method: 'POST',
                headers:headers,
                body: JSON.stringify(data),
            });
            const json = await response.json();
            return json;
        } catch (error) {
            console.error(error);
        }
    },
    ApplyCouponCode: async (data) => {
        try {
            const response = await fetch(apiUrl + ApplyCouponCode, {
                method: 'POST',
                headers:headers,
                body: JSON.stringify(data),
            });
            const json = await response.json();
            return json;
        } catch (error) {
            console.error(error);
        }
    },
    getPaymentMethodList: async (billerid) => {
        try {
            const response = await fetch(apiUrl + getPaymentMethod+"?billerid="+billerid, {
                method: 'POST',
                headers:headers,
            });
            const json = await response.json();
            return json?.paymethodoption;
        } catch (error) {
            console.error(error);
        }
    }

};